import React from "react";
import { useSelector } from "react-redux";
import { MDBBreadcrumb, MDBBreadcrumbItem, MDBRow, MDBCol } from "mdbreact";
import ProdutoItem from "./ProdutoItem";

function CompreAqui() {
  const camarotes = useSelector(state => state.products.camarotes);
  const blocos = useSelector(state => state.products.blocos);

  return (
    <div className="mb-5">
      <h1 className="text-white">.</h1>
      <MDBBreadcrumb>
        <MDBBreadcrumbItem>Home</MDBBreadcrumbItem>
        <MDBBreadcrumbItem active>Compre Aqui</MDBBreadcrumbItem>
      </MDBBreadcrumb>
      <h5>Monte o carnaval do seu jeito</h5>
      <h5 className="h1-responsive font-weight-bold text-left my-2">
        CAMAROTES
        <MDBRow>
          {camarotes
            .filter(function(produtos) {
              return produtos.type === "grouped";
            })
            .map(item => (
              <MDBCol key={item.id} size="4" className="my-2">
                <ProdutoItem dados={item} />
              </MDBCol>
            ))}
        </MDBRow>
      </h5>
      <h5 className="h1-responsive font-weight-bold text-left my-2">
        BLOCOS
        <MDBRow>
          {blocos
            .filter(function(produtos) {
              return produtos.type === "grouped";
            })
            .map(item => (
              <MDBCol key={item.id} size="4" className="my-2">
                <ProdutoItem dados={item} />
              </MDBCol>
            ))}
        </MDBRow>
      </h5>
    </div>
  );
}

export default CompreAqui;
