import React from "react";
import { useDispatch } from "react-redux";
import { MDBCard } from "mdbreact";
import { addItemAction } from "../../actions";

export default function ProdutoItem(props) {
  const produto = props.dados;
  const imgurl = produto.images[0] ? produto.images[0].src : "";
  const dispatch = useDispatch();

  function addItem() {
    dispatch(addItemAction(produto));
  }

  return (
    <MDBCard
      className="card-image"
      style={{
        background: `#CDCDCD url('${imgurl}')`,
        backgroundSize: "cover"
      }}
      onClick={addItem}
    >
      <div className="text-white text-left d-flex align-items-bottom py-5 px-4"></div>
    </MDBCard>
  );
}
